export default function advantages() {

	const advantagesContentBlock = $('.advantages_img-block__js');
	const imgBlock = $('.advantages_img__js');
	const textBlock = $('.advantages_img-text__js');

	if ($(window).width() > '767') {

		//       .   \ 
		//          
		const changeActiveItem = (src, text) => {
			advantagesContentBlock.animate({ opacity: 0 }, 200);
			setTimeout(() => {
				imgBlock.attr('src', src);
				textBlock.text(text);
				advantagesContentBlock.animate({ opacity: 1 }, 200);
			}, 200);
		}

		if ($('.advantages__js').length > 0) {

			for (let i = 0; i < $('.advantages_list-item__js').length; i++) {
				document.querySelectorAll('.advantages_list-item__js')[i].addEventListener('click', function () {

					//      -   
					if ($(this).hasClass('active')) return

					else {

						$('.advantages_list-item__js').removeClass('active');
						$(this).addClass('active');

						if (i === 0) {
							changeActiveItem(
								'./img/advantages-img1.svg',
								'              .'
							)
						} else if (i === 1) {
							changeActiveItem(
								'./img/advantages-img2.svg',
								'  ,         .      '
							)
						} else if (i === 2) {
							changeActiveItem(
								'./img/advantages-img3.svg',
								'    50        ,    !'
							)
						} else if (i === 3) {
							changeActiveItem(
								'./img/advantages-img4.svg',
								'   ,     .    ,     .'
							)
						} else if (i === 4) {
							changeActiveItem(
								'./img/advantages-img5.svg',
								'  ,     ,     ,   '
							)
						} else if (i === 5) {
							changeActiveItem(
								'./img/advantages-img6.svg',
								'    ,     !'
							)
						} else if (i === 6) {
							changeActiveItem(
								'./img/advantages-img7.svg',
								'     1500-3000  ,    ,     .'
							)
						}
					};
				});
			};
		};
	}

	//  
	else {

		$('.advantages_list__js').slick({
			infinite: false,
			slidesToShow: 1,
			slidesToScroll: 1,
			arrows: false,
			dots: true,
			appendDots: $('.advantages__js'),
		})
	}
};