export function steps() {

	if ($(".Container__progress-bar__js").length > 0) {

		const infoItem = $(".Steps_info-items__js");
		const infoTitle = $(".Steps_info-title__js");
		const infoText = $(".Steps_info-text__js");
		let state = 0;
		let maxState = $(".cont span").length - 1;

		//         
		function makeUI() {
			if (state === 0) {
				infoItem.animate({ opacity: 0 }, 100);
				$('#progress-bar').attr("value", "0")
				setTimeout(() => {
					//        ""
					infoTitle.text(" ");
					infoText.text(" ,     ()       ,     .");
					infoItem.animate({ opacity: 1 }, 100);
				}, 100);
			}

			else if (state === 1) {
				infoItem.animate({ opacity: 0 }, 100);
				$('#progress-bar').attr("value", "20")
				setTimeout(() => {
					infoTitle.text("  ");
					infoText.text(" ,   ,          .");
					infoItem.animate({ opacity: 1 }, 100);
				}, 100);
			}

			else if (state === 2) {
				$('#progress-bar').attr("value", "40")
				infoItem.animate({ opacity: 0 }, 100);
				setTimeout(() => {
					infoTitle.text(" ");
					infoText.text("         :  .    ,    ,        .");
					infoItem.animate({ opacity: 1 }, 100);
				}, 100);;
			}

			else if (state === 3) {
				$('#progress-bar').attr("value", "60")
				infoItem.animate({ opacity: 0 }, 100);
				setTimeout(() => {
					infoTitle.text(" 4");
					infoText.text("  ,       .   !       ");
					infoItem.animate({ opacity: 1 }, 100);
				}, 100);
			}

			else if (state === 4) {
				$('#progress-bar').attr("value", "80")
				infoItem.animate({ opacity: 0 }, 100);
				setTimeout(() => {
					infoTitle.text(" 5");
					infoText.text("      ,       ( -     )");
					infoItem.animate({ opacity: 1 }, 100);
				}, 100);
			}

			else if (state === 5) {
				$('#progress-bar').attr("value", "100")
				infoItem.animate({ opacity: 0 }, 100);
				setTimeout(() => {

					infoTitle.text("        ");
					infoText.text("         .              ");
					infoItem.animate({ opacity: 1 }, 100);
				}, 100);
			}

			$(".state-active").removeClass("state-active");
			$(`.bar-item:eq(${state})`).addClass("state-active");
		}

		//        " "
		$(".cont span").click(function () {
			$(this).prevAll().addClass("blue");
			$(this).nextAll().removeClass("blue");
			$(this).removeClass("blue");
			state = $(this).data("id");
			makeUI();
		});


		//    
		function mobileStepAnim () {
			const progressBarContainer = $('.cont__js');

			if ($(window).width() <= '767') {
				let barItem = document.querySelectorAll('.bar-item__js');

				for (var i = 0; i < barItem.length; i++) {

					let currentBarPos

					//            
					switch (i) {
						case 0: currentBarPos = "0%"; break;
						case 1: currentBarPos = "-5%"; break;
						case 2: currentBarPos = "-40%"; break;
						case 3: currentBarPos = "-60%"; break;
						case 4: currentBarPos = "-95%"; break;
						case 5: currentBarPos = "-105%"; break;
					}

					barItem[i].addEventListener('click', () => {
						progressBarContainer.css('left', currentBarPos);
					})
				}
			};
		}
		
		mobileStepAnim();
		
		$(window).resize(mobileStepAnim)
	};
};